.parts-catalog {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.parts-catalog .summary-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.parts-catalog .summary-cards-row .summary-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  gap: 1rem;
}
.parts-catalog .summary-cards-row .summary-card .card-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.parts-catalog .summary-cards-row .summary-card .card-content .card-value {
  font-size: 1.875rem;
  font-weight: 700;
  color: #111827;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.parts-catalog .summary-cards-row .summary-card .card-content .card-value.warning {
  color: #f59e0b;
}
.parts-catalog .summary-cards-row .summary-card .card-content .card-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.parts-catalog .summary-cards-row .summary-card.total-parts .card-icon {
  background-color: #dbeafe;
  color: #3b82f6;
}
.parts-catalog .summary-cards-row .summary-card.low-stock .card-icon {
  background-color: #fef3c7;
  color: #f59e0b;
}
.parts-catalog .summary-cards-row .summary-card.total-value .card-icon {
  background-color: #d1fae5;
  color: #10b981;
}
.parts-catalog .search-filters-section {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  margin-bottom: 2rem;
}
.parts-catalog .search-filters-section .search-row {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  align-items: center;
}
.parts-catalog .search-filters-section .search-row .search-container {
  flex: 1;
  position: relative;
}
.parts-catalog .search-filters-section .search-row .search-container .search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
}
.parts-catalog .search-filters-section .search-row .search-container .search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: #f9fafb;
}
.parts-catalog .search-filters-section .search-row .search-container .search-input:focus {
  outline: none;
  border-color: #3b82f6;
  background-color: white;
}
.parts-catalog .search-filters-section .search-row .search-container .search-input::placeholder {
  color: #9ca3af;
}
.parts-catalog .search-filters-section .search-row .advanced-filters-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: white;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}
.parts-catalog .search-filters-section .search-row .advanced-filters-btn:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.parts-catalog .search-filters-section .search-row .advanced-filters-btn svg {
  color: #6b7280;
}
.parts-catalog .search-filters-section .actions-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox input[type=checkbox] {
  display: none;
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox .checkmark {
  width: 1.25rem;
  height: 1.25rem;
  border: 2px solid #d1d5db;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox .checkmark::after {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  background-color: white;
  border-radius: 0.125rem;
  transform: scale(0);
  transition: transform 0.2s;
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox input[type=checkbox]:checked + .checkmark {
  background-color: #3b82f6;
  border-color: #3b82f6;
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox input[type=checkbox]:checked + .checkmark::after {
  transform: scale(1);
}
.parts-catalog .search-filters-section .actions-row .bulk-actions .bulk-checkbox .bulk-text {
  font-size: 0.875rem;
  color: #374151;
  font-weight: 500;
}
.parts-catalog .search-filters-section .actions-row .view-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.parts-catalog .search-filters-section .actions-row .view-controls .view-toggles {
  display: flex;
  background-color: #f3f4f6;
  border-radius: 0.5rem;
  padding: 0.25rem;
}
.parts-catalog .search-filters-section .actions-row .view-controls .view-toggles .view-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  background: none;
  border: none;
  border-radius: 0.375rem;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s;
}
.parts-catalog .search-filters-section .actions-row .view-controls .view-toggles .view-toggle:hover {
  color: #374151;
  background-color: #e5e7eb;
}
.parts-catalog .search-filters-section .actions-row .view-controls .view-toggles .view-toggle.active {
  background-color: #3b82f6;
  color: white;
}
.parts-catalog .search-filters-section .actions-row .view-controls .view-toggles .view-toggle svg {
  width: 1rem;
  height: 1rem;
}
.parts-catalog .search-filters-section .actions-row .view-controls .add-part-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.parts-catalog .search-filters-section .actions-row .view-controls .add-part-btn:hover {
  background-color: #2563eb;
}
.parts-catalog .search-filters-section .actions-row .view-controls .add-part-btn svg {
  color: white;
}
.parts-catalog .parts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}
.parts-catalog .parts-grid .part-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.parts-catalog .parts-grid .part-card .part-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}
.parts-catalog .parts-grid .part-card .part-header .part-info .part-name {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.25rem 0;
}
.parts-catalog .parts-grid .part-card .part-header .part-info .part-id {
  display: block;
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 0.25rem;
}
.parts-catalog .parts-grid .part-card .part-header .part-info .part-description {
  display: block;
  font-size: 0.875rem;
  color: #9ca3af;
}
.parts-catalog .parts-grid .part-card .part-header .stock-status .status-badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  color: white;
}
.parts-catalog .parts-grid .part-card .part-header .stock-status .status-badge.out-of-stock {
  background-color: #ef4444;
}
.parts-catalog .parts-grid .part-card .part-header .stock-status .status-badge svg {
  color: white;
}
.parts-catalog .parts-grid .part-card .part-details {
  margin-bottom: 1.5rem;
}
.parts-catalog .parts-grid .part-card .part-details .detail-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid #f3f4f6;
}
.parts-catalog .parts-grid .part-card .part-details .detail-row:last-child {
  border-bottom: none;
}
.parts-catalog .parts-grid .part-card .part-details .detail-row .detail-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.parts-catalog .parts-grid .part-card .part-details .detail-row .detail-value {
  font-size: 0.875rem;
  color: #111827;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.parts-catalog .parts-grid .part-card .part-details .detail-row .detail-value svg {
  color: #9ca3af;
}
.parts-catalog .parts-grid .part-card .part-actions {
  display: flex;
  gap: 0.75rem;
}
.parts-catalog .parts-grid .part-card .part-actions .action-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.parts-catalog .parts-grid .part-card .part-actions .action-btn.edit {
  background-color: #3b82f6;
  color: white;
}
.parts-catalog .parts-grid .part-card .part-actions .action-btn.edit:hover {
  background-color: #2563eb;
}
.parts-catalog .parts-grid .part-card .part-actions .action-btn.delete {
  background-color: #ef4444;
  color: white;
}
.parts-catalog .parts-grid .part-card .part-actions .action-btn.delete:hover {
  background-color: #dc2626;
}
.parts-catalog .parts-grid .part-card .part-actions .action-btn svg {
  color: currentColor;
}
.parts-catalog .fab {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.parts-catalog .fab:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.parts-catalog .fab svg {
  color: white;
}
